import React from 'react'
import styled from 'styled-components'
import Resume from './../assets/LindaCV.pdf'

const Home = () => {
    return (
        <HomeContainer>
            <Title3>Hello, my name is</Title3>
            <Title1>Linda Franco</Title1>
            <Title2>And I'm a Front-End Developer</Title2>
            <a href={Resume} download="LindaCV">
                Download Resume
            </a>

            <p>- If you want it, you will get it -</p>
        </HomeContainer>
    );
}

const HomeContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    
    a {
        text-decoration: none;
        color: #f2f2f2;
        padding: 10px;
        background-color: #912f56;
        transition: all .3s ease;
        border-radius: 4px;
        margin-top: 10px;
    }

    a:hover {
        background-color: #17bebb;
    }

    p {
        padding-top: 3rem;
        font-size: 2.5rem;
        font-style: italic;
        font-weight: 600;
        color: #17bebb;
    }

    @media screen and (max-width: 740px) {
        p {
            text-align: center;
            padding: 3rem 3rem;
        }
    }

    @media screen and (max-width: 500px) {
        p {
            font-size: 1.8rem;
        }
    }

    @media screen and (max-width: 380px) {
        p {
            font-size: 1.8rem;
            padding-bottom: 0;
        }
    }
`

const Title1 = styled.h1`
    font-size: 4rem;

    @media screen and (max-width: 500px) {
        text-align: center;
        font-size: 3rem;
    }
`
const Title2 = styled.h2`
    font-size: 2rem;

    @media screen and (max-width: 740px) {
        text-align: center;
    }

    @media screen and (max-width: 500px) {
        font-size: 1.5rem;
    }
`
const Title3 = styled.h3`
    font-size: 1rem;
`

export default Home;