import React from 'react'
import styled from 'styled-components'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faGithub, faLinkedinIn, faInstagram } from '@fortawesome/free-brands-svg-icons'

const Footer = () => {
    return (
        <FooterContainer>
            <FooterButton>
                <a href="https://github.com/lindafranco96" target="_blank" rel="noreferrer" >
                    <FontAwesomeIcon icon={faGithub} />
                </a>
            </FooterButton>
            <FooterButton>
                <a href="https://www.linkedin.com/in/lindafrancoprieto/" target="_blank" rel="noreferrer" >
                    <FontAwesomeIcon icon={faLinkedinIn} />
                </a>
            </FooterButton>
            <FooterButton>
                <a href="https://www.instagram.com/lindahyuk/" target="_blank" rel="noreferrer" >
                    <FontAwesomeIcon icon={faInstagram} />
                </a>
            </FooterButton>
        </FooterContainer>
    );
}

const FooterContainer = styled.footer`
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 5px 0;

    @media screen and (max-width: 380px) {
        
    }
`

const FooterButton = styled.button`
    padding: 3px;
    margin: 5px 20px;
    font-size: 20px;
    border-radius: 50%;
    border: none;
    width: 2.5rem;
    height: 2.5rem;
    transition: all .3s ease;
    background-color: #f2f2f2;

    a{
        color: #912f56;
    }

    &:hover {
        background-color: #17bebb;
        transform: scale(1.1);
    }

    &:focus {
        outline: none;
        border: none;
    }
`

export default Footer;