import React from 'react'
import styled from 'styled-components'
import ContainerTitle from './../assets/styles/Title'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCode, faPenNib, faMobileAlt } from '@fortawesome/free-solid-svg-icons'

const Services = () => {
    return (
        <div>
            <ContainerTitle>
                <h1>My Services</h1>
                <p>- what I Offer -</p>
            </ContainerTitle>
            <ServicesContainer>
                <ServicesContent>
                    <FontAwesomeIcon icon={faCode} className="services__icon" />
                    <h3>Web Design</h3>
                    <p>This service that I offer is something that I have dedicated time to improve.</p>
                </ServicesContent>
                <ServicesContent>
                    <FontAwesomeIcon icon={faPenNib} className="services__icon" />
                    <h3>Logo Design</h3>
                    <p>This service that I offer is of high quality, and as you can see I did my own logo for this web page</p>
                    <p>In my free time a hobby I have is drawing.</p>
                </ServicesContent>
                <ServicesContent>
                    <FontAwesomeIcon icon={faMobileAlt} className="services__icon" />
                    <h3>Apps Design</h3>
                    <p>This service that I offer needs improvements and more experience and it's something that I'll achieve.</p>
                    <p>I can make responsive web pages that adapt to any device.</p>
                </ServicesContent>
            </ServicesContainer>
        </div>
    );
}

const ServicesContainer = styled.div`
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    width: 100%;

    @media screen and (max-width: 1076px) {
        grid-template-columns: repeat(3, 1fr);
        padding-top: 70px;
    }

    @media screen and (max-width: 950px) {
        /* width: 100%; */
        /* grid-template-rows: repeat(2, 1fr); */
        display: flex;
        flex-direction: column;
        padding-top: 0;
    }
`

const ServicesContent = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 3rem 1.5rem 1.5rem 1.5rem;
    padding: 1.5rem .5rem;
    border: 2px solid #912f56;
    border-radius: .5rem;
    text-align: center;
    align-items: center;

    .services__icon {
        font-size: 3rem;
        width: 50px;
        padding: .5rem;
        background-color: #912f56;
        color: #f2f2f2;
        border-radius: 50%;
        margin-bottom: 1rem;
    }

    h3 {
        font-size: 1.25rem;
        color: #593821;
        margin-bottom: .5rem;
    }

    p {
        padding: 10px 30px;
    }

    &, .services__icon, h3, p {
        transition: .5s;
    }

    &:hover {
        background-color: #912f56;
    }

    &:hover .services__icon {
        background-color: #F2F2F2;
        color: #912f56;
    }

    &:hover h3, &:hover p {
        color: #F2F2F2;
    }

    @media screen and (max-width: 1076px) {
        margin: 5px;
        margin-top: 1rem;
        width: 250px;
        height: 300px;
    }

    @media screen and (max-width: 950px) {
        width: 100%;
        height: 220px;
    }

    @media screen and (max-width: 600px) {
        height: 250px;
    }

    @media screen and (max-width: 480px) {
        height: 280px;
    }

    @media screen and (max-width: 400px) {
        height: 300px;
    }
`


export default Services;