import React from 'react'
import styled from 'styled-components'
import { NavLink } from 'react-router-dom'
import linda_logo from './../assets/img/linda_logo.svg'
import ButtonNav from './ButtonNav'

const Header = () => {
    return (
        <HeaderContainer>
            <NavLink to="/" exact={true}>
                <Img>
                    <img src={linda_logo} alt="Linda-Logo" />
                </Img>
            </NavLink>
            <Menu>
                <NavLink to="/My-portfolio/" exact={true}>Home</NavLink>
                <NavLink to="/My-portfolio/about">About</NavLink>
                <NavLink to="/My-portfolio/skills">Skills</NavLink>
                <NavLink to="/My-portfolio/services">Services</NavLink>
                <NavLink to="/My-portfolio/projects">Projects</NavLink>
            </Menu>

            <ButtonNav />
        </HeaderContainer>
    );
}

const HeaderContainer = styled.header`
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 30px;
`

const Img = styled.div`
    padding-top: .5rem;
    padding-left: .8rem;
    width: 100px;
    height: auto;
    background-color: #912f56;
    border-radius: 50%;
    overflow: hidden;

    img {
        width: 100%;
    }

    img:hover {
        cursor: pointer;
    }
`

const Menu = styled.nav`
    a {
        color: cyan;
        padding: 10px 15px;
        font-size: 25px;
        color: #17bebb;
        text-decoration: none;
        font-weight: 600;
        transition: all .3s ease-in-out;
    }

    a:hover {
        color: #f2f2f2;
    }

    a.active {
        border-bottom: 3px solid #f2f2f2;
        padding-bottom: 3px;
        color: #f2f2f2;
    }

    @media screen and (max-width: 770px) {
        display: none;
    }
`

export default Header;