import React from 'react'
import styled from 'styled-components'
import ContainerTitle from '../assets/styles/Title'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faLink } from '@fortawesome/free-solid-svg-icons'
import { faGithubSquare } from '@fortawesome/free-brands-svg-icons'
import Todo_List_App from './../assets/img/Todo-List-App.PNG'

const Projects = () => {
    return (
        <div>
            <ContainerTitle>
                <h1>My Projects</h1>
                <p>- recent works -</p>
            </ContainerTitle>
            <ProjectsContainer>
                <ProjectContent>
                    <img src={Todo_List_App} alt="TodoList" />
                    <div className="works__data">
                        <div className="works__link-group">
                            <a href="https://lindafranco96.github.io/React-todo-list/" target="_blank" rel="noreferrer" className="works__link"><FontAwesomeIcon icon={faLink} className="projects__icon" /></a>
                            <a href="https://github.com/lindafranco96/React-todo-list" target="_blank" rel="noreferrer" className="works__link"><FontAwesomeIcon icon={faGithubSquare} className="projects__icon" /></a>
                        </div>
                        <span className="works__title">Project 1</span>
                        <div className="works__description">
                            <span>React</span>
                            <span>LocalStorage</span>
                            <span>Hooks</span>
                        </div>
                    </div>
                </ProjectContent>
            </ProjectsContainer>
        </div>
    );
}

const ProjectsContainer = styled.div`
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 2rem;
    margin-right: 1rem;
    justify-items: center;
    align-items: center;
    row-gap: 2rem;
    margin-top: 20px;
    height: 50vh;

    @media screen and (max-width: 950px) {
        display: flex;
        justify-content: center;
    }
`

const ProjectContent = styled.div`
    position: relative;
    overflow: hidden;
    border-radius: .5rem;
    background-color: #377696;
    width: 350px;
    height: 350px;

    img {
        /* width: 350px;
        height: 350px; */
        width: 100%;
        height: 100%;
    }

    .works__data {
        position: absolute;
        bottom: -100%;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: #cc2c77b3;
        border-radius: .5rem;
        transition: .3s;
    }

    .works__link {
        display: inline-flex;
        font-size: 1.5rem;
        background-color: #f2f2f2;
        color: #912f56;
        padding: .25rem;
        border-radius: .25rem;
        margin-bottom: 1rem;
    }

    .works__link-group {
        display: flex;
        flex-direction: row;

        a {
            margin: 10px;
        }
    }

    .works__title {
        font-size: 1.5rem;
        color: #f2f2f2;
    }

    .works__description {
        margin: 10px;

        span {
            background-color: #17bebb;
            border-radius: .2rem;
            margin: 5px;
            padding: 5px;
            color: #912f56;
        }
    }

    &:hover .works__data {
        bottom: 0;
    }

    @media screen and (max-width: 500px) {
        width: 320px;
        height: 300px;

        /* img {
            width: 320px;
            height: 300px;
        } */
    }

    @media screen and (max-width: 430px) {
        width: 315px;
        height: 300px;

        /* img {
            width: 280px;
            height: 300px;
        } */
    }

    @media screen and (max-width: 410px) {
        width: 280px;
        height: 300px;

        /* img {
            width: 280px;
            height: 300px;
        } */
    }

    @media screen and (max-width: 400px) {
        width: 230px;
        height: 300px;

        /* img {
            width: 230px;
            height: 300px;
        } */
    }
`

export default Projects