import React from 'react';
import styled from 'styled-components'
import { BrowserRouter, Route, Switch } from 'react-router-dom'
import Header from './components/Header'
import Home from './components/Home'
import AboutMe from './components/AboutMe'
import Skills from './components/Skills'
import Services from './components/Services'
import Projects from './components/Projects'
import Footer from './components/Footer'

function App() {
  return (
    <BrowserRouter>
      <MainContainer>
        <Header />

        <Main>
          <Switch>
            <Route path="/My-portfolio/about" component={AboutMe} />
            <Route path="/My-portfolio/skills" component={Skills} />
            <Route path="/My-portfolio/services" component={Services} />
            <Route path="/My-portfolio/projects" component={Projects} />
            <Route path="/My-portfolio/" component={Home} />
          </Switch>

        </Main>

        <Footer />

      </MainContainer>
    </BrowserRouter>
  );
}

const MainContainer = styled.div`
  width: 90%;
  max-width: 1300px;
  height: 100%;
  margin-bottom: 2rem;
  background-color: #912f56;
  border-radius: 10px;
  box-shadow: 0 0 5px red;

  /* @media screen and (max-width: 1076px) { */
    /* height: 100%; */
    /* height: 100vh;
    background-color: blue; */
  /* } */
  /* @media screen and (max-width: 950px) {
    height: auto;
  } */
`

const Main = styled.main`
  background: #f2f2f2;
  padding: 40px;
  border-radius: 10px;
  /* box-shadow: 0 0 5px rgba(129, 129, 129, 0.1); */
  width: 100%;
  height: 75vh;

  /* @media screen and (max-width: 950px) {
    height: auto;
  } */
  @media screen and (max-width: 950px) {
    overflow-y: auto;
    overflow-x: hidden;
  }

  @media screen and (max-width: 380px) {
    height: 77%;
  }
`

export default App;
