import React from 'react'
import styled from 'styled-components'
import linda_logo from './../assets/img/linda_logo.svg'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUser, faPhone, faEnvelope } from '@fortawesome/free-solid-svg-icons'
import ContainerTitle from './../assets/styles/Title'

const AboutMe = () => {
    return (
        <div>
            <ContainerTitle>
                <h1>About Me</h1>
                <p>- who I am -</p>
            </ContainerTitle>
            <InfoContainer>
                <img src={linda_logo} alt="" />
                <div>
                    <Info>I'm a frontend developer, passionate about creating and developing web interfaces. I have some experience in this area of work, with some quality projects.</Info>
                    <br />
                    <Info>This logo was created by me.</Info>
                    <div>
                        <SubTitle>Information</SubTitle>
                        <IconText>
                            <FontAwesomeIcon icon={faUser} className="icon" />
                            <p>Linda Franco Prieto</p>
                        </IconText>
                        <IconText>
                            <FontAwesomeIcon icon={faPhone} className="icon" />
                            <p>+52 614 245 2958</p>
                        </IconText>
                        <IconText>
                            <FontAwesomeIcon icon={faEnvelope} className="icon" />
                            <p>Linda_Franco96@hotmail.com</p>
                        </IconText>
                    </div>
                </div>
            </InfoContainer>
        </div>
    );
}

const InfoContainer = styled.div`
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;

    img {
        width: 100%;
        height: 400px;
    }

    @media screen and (max-width: 920px) {
        display: flex;
        flex-direction: column;
    }

    @media screen and (max-width: 720px) {
        img {
            width: 800px;
            height: 400px;
            width: 100%;
            margin-bottom: 1rem;
        }
    }
`

const Info = styled.p`
    font-size: 1.2rem;
    width: 100%;
    color: #17bebb;
    font-weight: 500;
    padding-right: 20px;

    @media screen and (max-width: 920px) {
        text-align: center;
    }

    @media screen and (max-width: 480px) {
        padding: 0 1.5rem;
    }

    @media screen and (max-width: 400px) {
        padding: 0 2rem;
    }
`

const SubTitle = styled.h3`
    font-size: 1.2rem;
    font-weight: 700;
    letter-spacing: 2.3px;
    margin: 10px 0;
    color: #2e1d11;

    @media screen and (max-width: 480px) {
        padding-left: 1rem;
    }

    /* @media screen and (max-width: 380px) {
        padding-left: 1rem;
    } */
`

const IconText = styled.div`
    display: flex;
    font-size: 1.5rem;
    margin-bottom: 10px;
    padding-left: 1.5rem;
    align-items: center;
    color: #17bebb;

    .icon {
        color: #912f56;
        margin-right: 1rem;
    }

    p {
        margin: 10px;
    }

    @media screen and (max-width: 500px) {
        p {
            font-size: 1.4rem;
        }
    }

    @media screen and (max-width: 400px) {
        p {
            font-size: 1rem;
        }
    }
`

export default AboutMe;